<template>
  <div class="d-flex justify-content-center align-items-center width-100vw">
    <form class="login-container m-3" @submit.prevent="postLogin()">
      <div class="login-header mb-3">
        <h5>CS Login</h5>
      </div>
      <div class="form-group">
        <label for="exampleInputEmail1">User Name</label>
        <input
          type="text"
          class="form-control"
          id="exampleInputEmail1"
          aria-describedby="emailHelp"
          placeholder="Enter username"
          v-model="formLogin.username"
        />
        <small id="emailHelp" class="form-text text-muted"
          >We'll never share your email with anyone else.</small
        >
      </div>
      <div class="form-group">
        <label for="exampleInputPassword1">Password</label>
        <input
          type="password"
          class="form-control"
          id="exampleInputPassword1"
          placeholder="Enter password"
          v-model="formLogin.password"
        />
      </div>
      <div class="d-flex justify-content-between">
        <div class="form-check">
          <input type="checkbox" class="form-check-input" id="exampleCheck1" />
          <label class="form-check-label" for="exampleCheck1"
            >Remember me</label
          >
        </div>
        <div class="form-forgot-password">Forgot password</div>
      </div>
      <button type="submit" class="btn btn-primary">Login</button>
    </form>
  </div>
</template>

<script>
import ApiRequest from "../services/ApiRequest";

export default {
  name: "Login",
  data() {
    return {
      formLogin: {
        username: "",
        password: "",
      },
    //   ApiRequest: null,
    };
  },
  methods: {
    postLogin() {
        this.ApiRequest.postLogin(this.formLogin);
    },
  },
  mounted() {
    this.ApiRequest = new ApiRequest();
  },
};
</script>

<style>
@import "../styles/authen/main.css";
</style>
